html {
  color: #000;
  --header-height: 60px;
  background-color: #161617cc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
}

body {
  margin: 0;
}

.header {
  backdrop-filter: saturate(1.8) blur(20px);
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
  position: relative;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  height: 40px;

  & img {
    width: 40px;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__toggler {
  display: none;
}

.nav__link {
  box-sizing: border-box;
  min-height: var(--header-height);
  color: inherit;
  text-transform: uppercase;
  align-items: center;
  text-decoration: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
